import React, { useEffect } from "react";
import './Header.css';
import Typewriter from 'typewriter-effect/dist/core';
import { /*isMobile, isTablet,*/ isDesktop } from "react-device-detect";

export default function Header() {
    useEffect(() => {
        new Typewriter('#typewriter', {
            strings: ['a Web Developer.', 'an App Developer.', 'a Student.'],
            autoStart: true,
            loop: true,
        });
    }, []);

    return (
        <header id="header" style={{ backgroundAttachment: isDesktop ? 'fixed' : 'inherit' }}>
            <div className="content">
                <h1>
                    Welcome,<br />
                    my name is Justin Storm,<br />
                    I am <span id="typewriter"></span>
                </h1>
                <button onClick={(e) => {e.preventDefault(); document.querySelector('#about')?.scrollIntoView()}}>Learn More</button>
            </div>
        </header>
    );
}
