export default function Navbar({ onToggle }) {
  const location = window.location.pathname;

  return (
    <nav>
      <a className="logo" href='/'>
        <img src={JroxLogo} alt="Logo" />
        <h1>Storm</h1>
      </a>
      <div className="links">
        {links.map((link, index) => (
          <a className={`link ${location === link.link ? 'active' : ''}`} href={link.link}>
            {link.icon}
            <span>{link.text}</span>
          </a>
        ))}
      </div>
      <a className="contact" href={contactButton.link}>
        {contactButton.icon}
        <span>{contactButton.text}</span>
      </a>
      <button className="toggler" onClick={onToggle}>
        <TextAlignJustify color="#fff" />
      </button>
    </nav>
  )
}
