import React from 'react';
import { links, contactButton } from './linkData';
import JroxLogo from '../images/jroxlogo.jpg';
import './Sidebar.css';
import { X } from 'lucide-react';
import { isDesktop } from 'react-device-detect';

export default function Sidebar({ open, onClose }) {
    const location = window.location.pathname;

    return (
        <aside className={`${open ? 'open' : ''}`}>
            <div className='header'>
                <a className="logo" href='/'>
                    <img src={JroxLogo} alt='Logo' />
                    <h1>Storm</h1>
                </a>
                <button onClick={onClose}>
                    <X color='#fff' />
                </button>
            </div>
            <hr />
            <div className={`content ${!isDesktop ? 'mobile' : ''}`}>
                <div className='links'>
                    {links.map((link, index) => (
                        <a key={index} className={`link ${location === link.link ? 'active' : ''}`} href={link.link}>
                            {link.icon}
                            <span>{link.text}</span>
                        </a>
                    ))}
                </div>
                <a href={contactButton.link} className='contact'>
                    {contactButton.icon}
                    <span>{contactButton.text}</span>
                </a>
            </div>
        </aside>
    )
}