import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Header from './components/Header';
import Gallery from './components/Gallery';
import Navbar from './components/Navbar';
import About from './components/About';
import Contact from './components/Contact';
import { useEffect, useState } from 'react';
import Sidebar from './components/Sidebar';
import Overlay from './components/Overlay';

function App() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false); 
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    function handleResize() {
      setWindowWidth(window.innerWidth);
    }

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [])

  useEffect(() => {
    if (windowWidth > 1000 && isSidebarOpen) {
      setIsSidebarOpen(false);
    }
  }, [windowWidth, isSidebarOpen]);

   return (
        <div className='App'>
          <Navbar onToggle={() => setIsSidebarOpen(!isSidebarOpen)} />
          <Sidebar open={isSidebarOpen} onClose={() => setIsSidebarOpen(false)} />
          <Overlay open={isSidebarOpen} onClick={() => setIsSidebarOpen(false)} />
          <Router basename='/portfolio25-26'>
            <Routes>
              <Route
                path='/' 
                element={
                  <div style={{ background: 'linear-gradient(#212529, #000)'}}>
                    <Header />
                    <About />
                    <Gallery />
                  </div>
                } 
              />
              <Route 
                path='/about'
                element={
                  <About />
                }
              />
              <Route 
                path='education'
                element={
                  <About />
                }
              />
              <Route 
                path='/contact'
                element={
                  <Contact />
                }
              />
            </Routes>
          </Router>
        </div>
  );
}

export default App;
